import React from 'react';

import { Col, FormGroup, Label } from 'reactstrap';
import AsyncSelect from 'react-select/async';

import api from '../../api'


const Supplier = (props) => {
	const promiseSupplierOptions = inputValue => {
		const url = `suppliers${inputValue ? '?filter[text]=' + inputValue : ''}`
		return api.get(url)
			.then(response => {
				let options = response.data.data.map(supplier => ({ value: supplier.id, label: supplier.attributes.business_name }));
				return options;
			})
			.catch(err => {
				console.log('some error', err);
			});
	};

	const handleChangeSupplier = (event) => {
		const value = event ? event.value : null
		props.onChange(value, 'supplier_id')
	}

	return (
		<Col md={6}>
			<FormGroup>
				<Label for="exampleUrl">Fornitore</Label>
				<AsyncSelect
					isClearable
					cacheOptions
					loadOptions={promiseSupplierOptions}
					defaultOptions
					onChange={(event) => handleChangeSupplier(event)}
				/>
			</FormGroup>
		</Col>
	);
};

export default Supplier;
