import Page from 'components/Page';
import PropTypes from 'prop-types';
import React from 'react';
import { connect } from 'react-redux';
import { Card, CardBody, CardHeader, Col, Row, Table, Form, Button, FormGroup, Label, Input } from 'reactstrap';

import * as actions from '../store/actions/index';
import DateRange from '../components/Filter/DateRange'
import AffiliateFilter from '../components/Filter/Affiliate'
import SupplierFilter from '../components/Filter/Supplier'
import NotificationSystem from 'react-notification-system';
import { NOTIFICATION_SYSTEM_STYLE } from 'utils/constants';
import RowNoData from '../components/RowNoData';
import BasePage from './BasePage';

class SupplierDocPage extends BasePage {

	state = {
		filters: {
			affiliate_id: null,
			supplier_id: null,
			credit_note: false,
			from: null,
			to: null,
		}
	}

	handleSubmit = () => {
		const { searchClicked } = this.state
		this.setState({
			searchClicked: !searchClicked
		})
		this.props.onLoadStats(null, null, this.state.filters, ['affiliate', 'supplier'], null)
	}

	handleFilterChange = (value, name) => {
		console.log(value)
		const { filters } = this.state
		filters[name] = value
		this.setState({
			filters
		})
	}

	render() {
		let {
			stats,
			affiliates,
			suppliers
		} = this.props
		let amountTot = 0
		let vatAmountTot = 0
		let total = 0
		let rows = <RowNoData colspan={6} />
		if (this.props.loading === false && this.props.error === false && this.props.stats !== null) {
			rows = stats.map((row, index) => {
				const amount = parseFloat(row.attributes.amount.replace(',', '.'))
				const amountPrevYear = parseFloat(row.attributes.amount_prev_year.replace(',', '.'))
				const vat = parseFloat(row.attributes.vat_amount.replace(',', '.'))
				const percVariation = (((amount - amountPrevYear) / amountPrevYear) * 100).toFixed(2)
				amountTot += amount - vat
				vatAmountTot += vat
				total += amount
				return <tr key={index}>
					<td>{affiliates[row.attributes.affiliate_id].name}</td>
					<td>{suppliers[row.attributes.supplier_id].code}</td>
					<td>{suppliers[row.attributes.supplier_id].business_name}</td>
					<td>{(amount - vat).toFixed(2)}</td>
					<td>{row.attributes.vat_amount}</td>
					<td>{row.attributes.amount}</td>
					<td>{isFinite(percVariation) ? `${percVariation}%` : '100%'}</td>
				</tr>
			})
		}
		return (
			<Page title="Fatturato fornitori" breadcrumbs={[{ name: 'fatturato fornitori', active: true }]}>
				<Row>
					<Col>
						<Card className="mb-3">
							<CardHeader>
								Filtri
                            </CardHeader>
							<CardBody>
								<Form onSubmit={this.handleSubmit}>
									<DateRange onChange={this.handleFilterChange.bind(this)} />
									<Row>
										<AffiliateFilter onChange={this.handleFilterChange.bind(this)} />
										<SupplierFilter onChange={this.handleFilterChange.bind(this)} />
									</Row>
									<Row>
										<Col md={6}>
											<FormGroup check>
												<Label check>
													<Input type="checkbox" name="credit_note"
														onChange={(event) => this.handleFilterChange(event.target.checked, 'credit_note')} />{' '}
													Senza note credito
        										</Label>
											</FormGroup>
										</Col>
									</Row>
									<Row>
										<Col>
											<Button
												color="primary"
												disabled={this.props.loading && this.state.searchClicked}
												onClick={this.handleSubmit}>
												Cerca
                                            </Button>
										</Col>
									</Row>
								</Form>
							</CardBody>
						</Card>
						<Card className="mb-3">
							<CardBody>
								<Table responsive>
									<thead>
										<tr>
											<th>Fialiale</th>
											<th>Codice</th>
											<th>Ragione sociale</th>
											<th>Imponibile</th>
											<th>Iva</th>
											<th>Totale</th>
											<th>% anno precedente</th>
										</tr>
									</thead>
									<tbody>
										{rows}
									</tbody>
									<tfoot>
										<tr>
											<th>TOTALI:</th>
											<td></td>
											<td></td>
											<td>€ {amountTot.toFixed(2)}</td>
											<td>€ {vatAmountTot.toFixed(2)}</td>
											<td>€ {(total).toFixed(2)}</td>
											<td></td>
										</tr>
									</tfoot>
								</Table>
							</CardBody>
						</Card>
					</Col>
				</Row>
				<NotificationSystem
					dismissible={false}
					ref={notificationSystem =>
						(this.notificationSystem = notificationSystem)
					}
					style={NOTIFICATION_SYSTEM_STYLE}
				/>
			</Page>
		);
	}
}
SupplierDocPage.contextTypes = {
	store: PropTypes.object
};

SupplierDocPage.propTypes = {
	notifications: PropTypes.array,
	stats: PropTypes.array,
	loading: PropTypes.bool,
	error: PropTypes.bool
};

const mapStateToProps = state => {
	return {
		stats: state.statistics.supplierDocStatistics,
		affiliates: state.statistics.affiliates,
		suppliers: state.statistics.suppliers,
		loading: state.statistics.loading,
		error: state.statistics.error,
		notifications: state.notifications,
	};
};

const mapDispatchToProps = dispatch => {
	return {
		onLoadStats: (page, perPage, filters, include, order) => dispatch(actions.loadSupplierDocStat(page, perPage, filters, include, order)),
	};
};
export default connect(mapStateToProps, mapDispatchToProps)(SupplierDocPage);
