import React from 'react';

import { Col, FormGroup, Label } from 'reactstrap';
import AsyncSelect from 'react-select/async';

import api from '../../api'


const Affiliate = (props) => {
    const promiseAffilialeOptions = inputValue => {
        const url = `affiliates${inputValue ? '?filter[text]=' + inputValue : ''}`
        return api.get(url)
            .then(response => {
                let options = response.data.data.map(affiliate => ({ value: affiliate.id, label: affiliate.attributes.name }));
                return options;
            })
            .catch(err => {
                console.log('some error', err);
            });
    };

    const handleChangeAffiliate = (event) => {
        const value = event ? event.value : null
        props.onChange(value, 'affiliate_id')
    }

    return (
        <Col md={6}>
            <FormGroup>
                <Label for="exampleUrl">Filiale</Label>
                <AsyncSelect
                    isClearable
                    cacheOptions
                    loadOptions={promiseAffilialeOptions}
                    defaultOptions
                    onChange={(event) => handleChangeAffiliate(event)}
                />
            </FormGroup>
        </Col>
    );
};

export default Affiliate;
